@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

#root {
  min-height: 100%;
  display: flex;
  position: relative;
}

.root {
  min-height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.root {
  flex-grow: 1;
  min-height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.container-height {
  flex: 1 0 auto;
  flex-grow: 1;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  font-family: Roboto; }
  header .brand {
    padding: 50px 0px; }
    header .brand p {
      text-align: center;
      letter-spacing: 0.2rem;
      font-size: 60px;
      font-weight: 300;
      color: #444040; }
      header .brand p span {
        font-size: 16px;
        letter-spacing: 0rem;
        font-weight: 400; }
  header .menu {
    width: 100%;
    background-color: #e45353; }
    header .menu ul {
      width: 100%;
      padding: 0px;
      display: flex;
      justify-content: center;
      margin: 0px; }
      header .menu ul li {
        font-size: 14px;
        padding: 25px 20px;
        font-weight: 500;
        list-style: none;
        color: white;
        -webkit-transition: 0.3s;
        transition: 0.3s;
        cursor: pointer; }
        header .menu ul li:hover {
          -webkit-transform: scale(0.9);
                  transform: scale(0.9);
          background-color: white;
          color: #e45353; }
  header .button-xs {
    display: none;
    position: absolute;
    right: 0px;
    top: 0px;
    margin: 10px;
    width: 50px;
    height: 50px; }

@media (max-width: 576px) {
  header .brand {
    padding: 20px 0px; }
    header .brand p {
      font-size: 20px; }
      header .brand p span {
        font-size: 15px; }
  header .menu {
    display: none; }
  header .button-xs {
    display: block; } }

footer {
  width: 100%;
  background-color: #445469;
  color: #fff;
  box-shadow: 0px 0px 3px 0px #cdcdcd; }
  footer .footer-brand {
    margin: 50px 0px;
    font-size: 30px;
    text-shadow: 1px 1px 1px grey;
    text-align: center; }
  footer .footer-links {
    display: flex;
    justify-content: center; }
    footer .footer-links ul {
      padding: 0px;
      margin: 0px 0px 50px 0px; }
      footer .footer-links ul li {
        margin: 10px 0px;
        text-align: center;
        list-style: none; }
  footer .footer-rights {
    padding: 20px 0px;
    width: 100%;
    background-color: #2f3b49;
    text-align: center; }

